.about {
    width: 100%;
    background-color: $darkBlueColor;
    position: relative;
    padding:  100px 0;
    @media (max-width: $md-4) {
        padding: 40px 0;
    }
    @media (max-width: $md-6) {
       padding: 0 0 40px 0;
    }
    // .about__container

    &__container {
        
    }

    // .about__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        color: #fff;
        font-family: "Rubik", sans-serif;
        @media (max-width: $md-4) {
            flex-direction: column;
        }
    }

    // .about__image

    &__image {
        width: 650px;
        height: 473px;
        object-fit: contain;
        @media (max-width: $md-2) {
            width: 450px;
            height: 400px;
        }
        @media (max-width: $md-4) {
           width: 100%;
           margin: 0 0 20px 0;
        } 
        @media (max-width: $md-6) {
            width: 100%;
            max-height: 240px;
         } 
    }

    // .about__content

    &__content {
        display: flex;
        flex-direction: column;
        margin: 0 0 0 70px;
        @media (max-width: $md-2) {
            margin: 0 0 0 30px;
        }

    }

    // .about__title

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 24px 0;
        max-width: 480px;
        @media (max-width: $md-3) {
            font-size: 35px;
        }
    }

    // .about__text

    &__text {
        color: $greyColor;
        font-size: 16px;
        margin: 0 0 30px 0;
        max-width: 500px;
        @media (max-width: $md-3) {
            font-size: 14px;
        }
    }

    // .about__button

    &__button {
        width: 146px;
        height: 59px;
        @media (max-width: $md-3) {
            height: 50px;
        }
    }

    // .about__background1

    &__background1 {
        position: absolute;
        top: 0;
        right: 0;
        @media (max-width: $md-4) {
            display: none;
         }
    }

    // .about__background2

    &__background2 {
        position: absolute;
        bottom: 0;
        right: 0;
        @media (max-width: $md-4) {
            display: none;
         }
    }

    // .about__line

    &__line {
        position: absolute;
        bottom: 0;
        @media (max-width: $md-4) {
           display: none;
        }
    }
}
