.earn {
    width: 100%;
    position: relative;
    // .earn__calculate

    &__calculate {
        left: 50%;
        top: 260px;
        width: 90%;
        margin-left: -45%;
        position: absolute;
        display: block;
        background-color: #fff;
        border-radius: 16px;
        padding: 60px 48px 48px 48px;
        font-family: "Rubik", sans-serif;
        box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.1);
        @media (max-width: $md-3) {
           padding: 50px 28px 38px 28px;
        }
        @media (max-width: $md-4) {
            max-height: 500px;
            top: 180px;
        }
        @media (max-width: $md-5) {
            top: 200px;
        }
    }

    // .earn__form

    &__form {
        display: flex;
        align-items: center;
        justify-content: space-between;
        margin-bottom: 90px;
        @media (max-width: $md-3) {
            margin-bottom: 50px;
        }
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: flex-start;
            margin-bottom: 30px;
        }
    }

    // .earn__input

    &__input {
        margin: 0 48px 0 0;
        border: none;
        border-bottom: 1px solid $greyColor;
        font-size: 20px;
        padding-bottom: 8px;
        outline: none;
        @media (max-width: $md-4) {
            margin: 0 0 20px 0;
            font-size: 18px;
        }
        &:focus {
            border-bottom: 1px solid $blueColor;
        }
    }
    &__input::placeholder {color: #000;}
    &__input:-ms-input-placeholder {color: #000;}
    &__input::-webkit-input-placeholder {color: #000;}
    &__input::-ms-input-placeholder {color: #000;}
    &__input:-moz-placeholder {color: #000;}
    &__input::-moz-placeholder {color: #000;}
    
    // .earn__hashrate

    &__hashrate {
        width: 70%;
        @media (max-width: $md-4) {
            width: 100%;
        }
    }

    // .earn__time

    &__time {
        width: 35%;
        @media (max-width: $md-4) {
            width: 100%;
        }
    }

    // .earn__button

    &__button {
        min-width: 147px;
        height: 59px;
        font-size: 18px;
        @media (max-width: $md-3) {
            height: 50px;
        }
        @media (max-width: $md-4) {
            width: 100%;
            height: 45px;
        } 
    }

    // .earn__content

    &__content {
        display: flex;
        flex-direction: column;
       
    }

    // .earn__caption

    &__caption {
        color: $blueColor;
        font-size: 16px;
        font-weight: 500;
        letter-spacing: 1px;
        margin: 0 0 16px 0;
        @media (max-width: $md-4) {
            font-size: 15px;
            margin: 0 0 10px 0;
        }
    }

    // .earn__result

    &__result {
        font-size: 32px;
        font-weight: 700;
        margin: 0 0 10px 0;
        @media (max-width: $md-4) {
            font-size: 28px;
        }
        span {
            color: $blueColor;
            margin: 0 0 0 5px;
        }
    }

    // .earn__info

    &__info {
        color: #828282;
        font-size: 16px;
        @media (max-width: $md-4) {
            font-size: 14px;
        }
    }

    // .earn__top

    &__top {
        width: 100%;
        height: 450px;
        background-color: $darkBlueColor;
        padding: 100px 0 0 0;
        @media (max-width: $md-4) {
            padding: 40px 0 0 0;
            height: 400px;
        }
    }

    // .earn__container

    &__container {
        
    }

    // .earn__body

    &__body {
        color: #fff;
        font-family: "Rubik", sans-serif;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .earn__title

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 24px 0;
        max-width: 750px;
        @media (max-width: $md-4) {
           font-size: 30px;
        }
    }

    // .earn__text

    &__text {
        color: $greyColor;
        font-size: 18px;
        margin: 0 0 64px 0;
        max-width: 622px;
        @media (max-width: $md-4) {
            font-size: 15px;
            max-width: 500px;
         }
    }

    // .earn__bottom

    &__bottom {
        width: 100%;
        height: 250px;
        background-color: #fff;
        @media (max-width: $md-3) {
            height: 150px;
        }
        @media (max-width: $md-4) {
            height: 190px;
        }
        @media (max-width: $md-5) {
            height: 230px;
        }
        @media (max-width: 360px) {
            height: 270px;
        }
    }
}
