.features {
    width: 100%;
    position: relative;
    padding: 100px 0;
    background-color: $violetColor;
    @media (max-width: $md-6) {
        padding: 40px 0;
    }
    // .features__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        color: #fff;
        font-family: "Rubik", sans-serif;
    }

    // .features__title

    &__header {
        display: flex;
        justify-content: center;
    }

    &__title {
        max-width: 758px;
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 100px 0;
        text-align: center;
        @media (max-width: $md-3) {
           font-size: 35px;
        }
        @media (max-width: $md-4) {
            font-size: 35px;
            margin: 0 0 70px 0;
         }
         @media (max-width: $md-6) {
            font-size: 30px;
         }
    }

    // .features__content

    &__content {
    }

    // .features__smart

    &__smart {
        width: 100%;
        margin: 0 0 120px 0;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
            margin: 0 0 60px;
        } 
      
    }

    // .features__smart-left

    &__smart-left {
        @media (max-width: $md-4) {
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
            margin: 0 0 40px 0;
        } 
    }

    // .features__smart-caption

    &__smart-caption {
        
    }

    // .features__caption

    &__caption {
        font-weight: 700;
        font-size: 32px;
        max-width: 452px;
        margin: 0 0 24px 0;
        @media (max-width: $md-2) {
            max-width: 350px;
         }
         @media (max-width: $md-4) {
            max-width: 550px;
        }  
    }

    // .features__smart-text

    &__smart-text {
       
    }

    // .features__text

    &__text {
        font-size: 16px;
        color: $greyColor;
        max-width: 408px;
        margin: 0 0 32px 0;
        @media (max-width: $md-2) {
           max-width: 300px;
        }
        @media (max-width: $md-4) {
            max-width: 500px;
        }  
    }

    &__button {
        width: 162px;
        height: 59px;
        font-size: 18px;
    }

    // .features__smart-button

    &__smart-button {
    }

    // .features__smart-image

    &__smart-image {
        @media (max-width: $md-2) {
            width: 400px;
        }
        @media (max-width: $md-4) {
          width: 70%;
        } 
    }

    // .features__statistic

    &__statistic {
        width: 100%;
        margin: 0 0 120px;
        display: flex;
        align-items: center;
        padding: 0 15px 0 0;
        @media (max-width: $md-4) {
            flex-direction: column-reverse;
            align-items: center;
            margin: 0 0 60px;
        } 
    }

    // .features__statistic-image

    &__statistic-image {
        @media (max-width: $md-2) {
            width: 400px;
        }
        @media (max-width: $md-4) {
            width: 80%;
        }
    }

    // .features__statistic-right

    &__statistic-right {
        margin: 0 0 0 90px;
        @media (max-width: $md-2) {
            margin: 0 0 0 30px;
        }
        @media (max-width: $md-4) {
            margin: 0 0 40px 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        }
    }

    // .features__statistic-caption

    &__statistic-caption {
    }

    // .features__statistic-text

    &__statistic-text {
    }

    // .features__statistic-button

    &__statistic-button {
    }

    // .features__profit

    &__profit {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
        } 
    }

    // .features__profit-left

    &__profit-left {
        @media (max-width: $md-4) {
            margin: 0 0 40px 0;
            display: flex;
            flex-direction: column;
            align-items: center;
            text-align: center;
        } 
    }

    // .features__profit-caption

    &__profit-caption {
    }

    // .features__profit-text

    &__profit-text {
    }

    // .features__profit-button

    &__profit-button {
    }

    // .features__profit-image

    &__profit-image {
        z-index: 2;
        @media (max-width: $md-2) {
            width: 400px;
        }
        @media (max-width: $md-4) {
           width: 100%;
        } 
    }

    // .features__circle1

    &__circle1 {
        position: absolute;
        top: 0;
        left: 150px;
        @media (max-width: $md-2) {
            left: 0;
        }
        @media (max-width: $md-4) {
            display: none;
        }  
    }

    // .features__circle2

    &__circle2 {
        position: absolute;
        bottom: 0;
        right: 0;
        z-index: 1;
        @media (max-width: $md-4) {
            display: none;
        }  
    }
}
