.first-screen {
    width: 100%;
    background-color: $darkBlueColor;
    padding: 130px 0 0 0;
    position: relative;
    overflow: hidden;
    // .first-screen__container

    &__container {
       
    }

    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
    }

    // .first-screen__main

    &__main {
        height: 100vh;
        display: flex;
        justify-content: space-between;
        font-family: "Rubik", sans-serif;
        @media (max-width: $md-2) {
            height: auto;
            margin: 0 0 60px 0;
        }
    }

    // .first-screen__content

    &__content {
        display: flex;
        flex-direction: column;
        margin: 0 30px 0 0;
    }

    // .first-screen__discount

    &__discount { 
        background-color: $darkBlue2Color;
        display: flex;
        align-items: center;
        width: 365px;
        height: 40px;
        border-radius: 32px;
        margin: 0 0 28px 0;
        padding: 0 5px;
        @media (max-width: $md-6) {
            flex-direction: column;
            align-items: center;
            width: 100%;
            margin: 0 10px;
            height: auto;
            background-color: transparent;
            margin: 0 0 30px 0;
        }
    }
 
    // .first-screen__percent

    &__percent {
        background-color: #fff;
        color: $darkBlueColor;
        width: 113px;
        height: 32px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-weight: 500;
        font-size: 16px;
        margin: 0 16px 0 0;
        border-radius: 32px;
        @media (max-width: $md-6) {
            width: 100%;
            margin: 0 0 10px 0;
        }
    }

    // .first-screen__discount-info

    &__discount-info {
        font-size: 16px;
    }

    // .first-screen__title

    &__title {
        max-width: 588px;
        font-weight: 700;
        font-size: 64px;
        margin:  0 0 24px 0;
        @media (max-width: $md-4) {
            font-size: 45px;
        }
        @media (max-width: $md-5) {
            font-size: 35px;
        }
    }

    // .first-screen__text

    &__text {
        color: $greyColor;
        font-size: 16px;
        margin: 0 0 32px 0;
        max-width: 435px;
        @media (max-width: $md-4) {
            font-size: 15px;
            
        }
    }

    // .first-screen__button

    &__button {
        width: 202px;
        height: 64px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: rgba(54, 113, 233, 0.7);
        @media (max-width: $md-4) {
            height: 50px;
        }
    }

    // .first-screen__button-text

    &__button-text {
        font-weight: 500;
        font-size: 18px;
        margin: 0  24px 0 0;
    }

    // .first-screen__button-ico

    &__button-ico {
        width: 32px;
        height: 32px;
        background-color: #fff;
        display: flex;
        align-items: center;
        justify-content: center;
        border-radius: 50%;
        span {
            width: 100%;
            height: 100%;
            @include bg("contain");
           
        }
    }

    // .first-screen__image

    &__image {
        width: 600px;
        height: 585px;
        @media (max-width: 1440px) {
            width: 500px;
            height: 400px;
        }
        @media (max-width: $md-3) {
            display: none;
        }
    }

    // .first-screen__items

    &__items {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-family: "Rubik", sans-serif;
        @media (max-width: $md-3) {
            flex-wrap: wrap;
            justify-content: center;
        }
        @media (max-width: 615px) {
            flex-wrap: wrap;
            justify-content: flex-start;
        }
    }

    // .first-screen__item

    &__item {
        display: flex;
        align-items: center;
        margin: 10px;
        @media (max-width: $md-3) {
            margin: 20px 10px;
        }
    }

    // .first-screen__ico

    &__ico {
        width: 80px;
        height: 80px;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: $darkBlue2Color;
        border-radius: 50%; 
        margin: 0 24px 0 0;
        span {
             width: 40px;
             height: 40px;
             @include bg("contain");
           
        }
    }

    // .first-screen__block

    &__block {
        display: flex;
        flex-direction: column;
    }

    // .first-screen__caption

    &__caption {
        font-size: 40px;
        font-weight: 700;
        margin: 0 0 4px 0;
        line-height: 60px;
        @media (max-width: $md-4) {
            font-size: 25px;
            line-height: 25px;
        }
    }

    // .first-screen__description

    &__description {
        color: $greyColor;
        font-size: 16px;
        @media (max-width: $md-6) {
           font-size: 14px;
         }
    }

    &__line1, &__line2 {
        display: none;
        position: absolute;
    }

    &__line1 {
       
    }
    &__line2 {
    
    }

}
