*,
*::before,
*::after {
	box-sizing: border-box;
}

.flex {
	display: flex;
	align-items: flex-start;
	justify-content: flex-start;
}

.--just-space {
	justify-content: space-between;
}

.--just-center {
	justify-content: center;
}

.--just-end {
	justify-content: flex-end;
}

.--align-str {
	align-items: stretch;
}

.--align-center {
	align-items: center;
}

.--align-end {
	align-items: flex-end;
}

.--dir-col {
	flex-direction: column;
}

html {
	font-size: 16px;
}

html,
body {
	min-height: 100%;
	position: relative;
}

body {
	font-size: 1rem;
}

ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6 {
	margin: 0;
}

// Vars
$container: 1200px + 30px;
$md-1: $container;
$md-2: 1039.98px;
$md-3: 991.98px;
$md-4: 767.98px;
$md-5: 479.98px;
$md-6: 399.98px;

$blueColor: #3671E9;
$violetColor: #2B076E;
$darkBlueColor: #0D0D2B;  
$darkBlue2Color: #151535;
$greyColor: #E0E0E0;
 
// Elements
.body.lock {
	overflow: hidden;
}

.container {
	max-width: $container;
	height: 100%;
	margin: 0 auto;
	padding: 0 15px;
}

.button {
	background-color: $blueColor;
	color: #fff;
	font-family: "Rubik", sans-serif;
	border-radius: 32px; 
	cursor: pointer;
	transition: .4s;
	outline: none;
	border: 1px solid transparent;
	&:hover, &:focus {
		background-color: #0055ff;
	}
}
 
.logo {
	display: flex;
	align-items: center;
	cursor: pointer;
	text-decoration: none;
    // .logo__ico

    &__ico { 
		width: 40px;
		height: 40px;
		@include bg("contain");
		margin: 0 16px 0 0;
		@media (max-width: $md-3) {
			margin: 0 8px 0 0;
		}
    }

    // .logo__text

    &__text {
		font-family: "Inter", sans-serif;
		font-weight: 600;
		font-size: 18px;
		color: #fff;
		letter-spacing: 1px;
    }
}
