.cryptocurrencies {
    width: 100%;
    padding: 20px 0 100px 0;
    // .cryptocurrencies__container

    &__container {
       
    }

    // .cryptocurrencies__body

    &__body {
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    // .cryptocurrencies__title

    &__title {
        font-weight: 700;
        font-size: 40px;
        margin: 0 0 64px 0;
        text-align: center;
        max-width: 741px;
        font-family: "Rubik", sans-serif;
        @media (max-width: $md-4) {
            font-size: 35px;
            margin: 0 0 40px 0;
        }
    }

    // .cryptocurrencies__block

    &__block {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            flex-direction: column;
            align-items: center;
            justify-content: center;
        }
    }

    // .cryptocurrencies__item

    &__item {
        max-width: 370px;
        border-radius: 8px;
        padding: 48px 24px;
        display: flex;
        flex-direction: column;
        align-items: center;
        text-align: center;
        margin: 0 22px;
        font-family: "Rubik", sans-serif;
        transition: all .3s linear 0s;
        background-color: #fff;
        &:hover, &:focus {
            color: #fff;
            background-color: $violetColor;
            .cryptocurrencies__arrow {
                display: none;
            }
            .cryptocurrencies__button {
                display: flex;
            }
        }
        @media (max-width: $md-3) {
            max-width: 100%;
            width: 80%;
            margin: 10px 22px;
            color: #fff;
            background-color: $violetColor;
            padding: 50px;
            .cryptocurrencies__arrow {
                display: none;
            }
            .cryptocurrencies__button {
                display: flex;
            }
        }
        @media (max-width: $md-6) {
            width: 100%;
        }
    }

    // .cryptocurrencies__ico

    &__ico {
        @include bg("contain");
        width: 80px;
        height: 80px;
        margin: 0 0 48px 0;
        border-radius: 50%;
        background-color: #fff;
    }

    // .cryptocurrencies__caption

    &__caption {
        font-weight: 700;
        font-size: 32px;
        margin: 0 0 16px 0;
        span {
            font-size: 18px;
            margin: 0 0 0 5px;
            color: $greyColor;
            text-transform: uppercase;
        }
    }

    // .cryptocurrencies__info

    &__info {
        max-width: 322px;
        font-size: 16px;
        margin: 0 0 25px 0;
        height: 56px;
        @media (max-width: $md-3) {
            height: 90px;
        }
    }

    // .cryptocurrencies__arrow

    &__arrow {
        width: 64px;
        height: 64px;
        background-color: transparent;
        border: 1px solid rgba(43, 7, 110, 0.2);
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        cursor: pointer;
        span {
            @include bg("contain");
            width: 48px;
            height: 48px;
        }
    }

    // .cryptocurrencies__button

    &__button {
        display: none;
        align-items: center;
        justify-content: center;
        width: 205px;
        height: 64px;
    }

    // .cryptocurrencies__button-text

    &__button-text {
        font-size: 18px;
        margin: 0 24px 0 0;
    }

    // .cryptocurrencies__button-arrow

    &__button-arrow {
        width: 32px;
        height: 32px;
        background-color: #fff;
        border-radius: 50%;
        display: flex;
        align-items: center;
        justify-content: center;
        span {
            width: 24px;
            height: 24px;
            @include bg("contain");
        }
    }
}

