.start {
    width: 100%;
    padding: 85px 0;
    position: relative;
    @media (max-width: $md-5) {
        padding: 55px 0;
    }
    // .start__body

    &__body {
        width: 100%;
        background-color: $blueColor;
        border-radius: 8px;
        padding: 48px;
        position: relative;
        color: #fff;
        font-family: "Rubik", sans-serif;
        display: flex;
        align-items: center;
        justify-content: space-between;
        @media (max-width: $md-3) {
            padding: 28px;
        } 
        @media (max-width: $md-5) {
           flex-direction: column;
           align-items: center;
        }
    }

    // .start__text

    &__text {
        display: flex;
        flex-direction: column;
        margin: 0 30px 0 0;
        z-index: 5;
        @media (max-width: $md-5) {
            margin: 0 0 30px 0;
         }
    }

    // .start__title

    &__title {
        font-weight: 700;
        font-size: 32px;
        max-width: 500px;
        margin: 0 0 16px 0;
        @media (max-width: $md-4) {
            font-size: 26px;
        }
    }

    // .start__info

    &__info {
        font-size: 16px;
        max-width: 348px;
        @media (max-width: $md-4) {
            font-size: 14px;
        }
    }

    // .start__form

    &__form {
        display: flex;
        align-items: center;
        z-index: 5;
        @media (max-width: $md-4) {
            flex-direction: column;
            width: 100%;
        }
    }

    // .start__input

    &__input {
        width: 374px;
        outline: none;
        background-color: transparent;
        border: 0;
        border-bottom: 1px solid #fff;
        color: #fff;
        padding-bottom: 7px;
        font-size: 16px;
        @media (max-width: $md-3) {
            width: 200px;
        }
        @media (max-width: $md-4) {
            width: 100%;
         } 
    }
    &__input::placeholder {color: #fff;}
    &__input:-ms-input-placeholder {color: #fff;}
    &__input::-webkit-input-placeholder {color: #fff;}
    &__input::-ms-input-placeholder {color: #fff;}
    &__input:-moz-placeholder {color: #fff;}
    &__input::-moz-placeholder {color: #fff;}
 
    // .start__button

    &__button {
        width: 152px;
        height: 55px;
        background-color: #fff;
        color: $darkBlueColor;
        font-size: 18px;
        margin: 0 0 0 40px;
        cursor: pointer;
        border: 1px solid transparent;
        border-radius: 32px;
        transition: .4s;
        @media (max-width: $md-4) {
           width: 100%;
           margin: 20px 0 0 0;
        } 
        &:hover, &:focus {
            background-color: #fff;
            color: $blueColor;
        }
    }
 
    // .start__ethereum

    &__ethereum {
        position: absolute;
        top: 0;
        @media (max-width: $md-5) {
            display: none;
        }
    }

    // .start__bitcoin

    &__bitcoin {
        position: absolute;
        right: 30px;
        bottom: 0;
        @media (max-width: $md-5) {
            display: none;
        }
    }

    // .start__background

    &__background {
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: -50;
    }
}
