.header {
    width: 100%;
    height: 60px;
    position: absolute; 
    margin: 16px 0 0 0;
    &::before {
        position: absolute;
        display: block;
        content: "";
        left: 0;
        top: 0;
        background-color: $darkBlueColor;
        width: 100%;
        height: 100%;
        z-index: 2;
    }

    // .header__container

    &__container {
        
    }

    // .header__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;  
        font-family: "Rubik", sans-serif;
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    // .header__logo

    &__logo {
        position: relative;
        z-index: 3;
    }

    // .header__burger
    &__burger.active {
        span {
            display: none;
        }
        &::before {
            top: 8px;
            transform: rotate(-45deg);
        }
        &::after {
            bottom : 8px;
            transform: rotate(45deg);
        }
    }
    &__burger {
        display: none;
        z-index: 3;
        cursor: pointer;
        width: 29px;
        height: 18px;
        position: relative;
        @media (max-width: $md-4) {
			display: block;
            &::before,
            &::after {
                position: absolute;
                content: "";
                display: block;
                width: 100%;
                height: 2px;
                background-color: #fff;
                left: 0;
                transition: .3s all ease 0s;
            }
            &::before {
                top: 0;
            }
            &::after {
                bottom: 0;
            }
            span {
                position: absolute;
                width: 100%;
                height: 2px;
                background-color: #fff;
                left: 0;
                top: 8px;
            }
		}
    }

    // .header__menu

    &__menu.active {
        left: 0;
    }
    &__menu {
        display: flex;
        align-items: center;
        flex-direction: row-reverse;
        z-index: 3;
        transition: all .3s linear 0s;
        @media (max-width: $md-4) {
            z-index: 1;
            width: 100%;
            height: 100%;
            background-color: $darkBlueColor;
            position: fixed;
            top: 0;
            left: -100%;
            padding: 130px 30px;
            flex-direction: column;
            align-items: center;
        }
        
    }

    // .header__block

    &__block {
        position: relative;
        z-index: 3;
        display: flex;
        align-items: center;
        @media (max-width: $md-4) {
            flex-direction: column;
            align-items: center;
            width: 100%;
            margin: 0 0 10px 0;
        }
    }

    // .header__login

    &__login {
        color: #fff;
        font-weight: 500;
        font-size: 16px;
        margin: 0 48px 0 0;
        text-decoration: none;
        position: relative;
        &::after {
            position: absolute;
            display: block;
            content: "";
            height: 24px;
            width: 1px;
            right: -24px;
            top: 0;
            background-color: rgba(242, 242, 242, 0.4);
        }
        @media (max-width: $md-3) {
			margin: 0 38px 0 0;
            &::after {
                right: -19px;
            }
		}
        @media (max-width: $md-4) {
            margin: 0 0 10px 0;
            color: $darkBlueColor;
            background-color: #fff;
            border-radius: 32px;
            display: flex;
            align-items: center;
            justify-content: center;
            font-size: 20px;
            width: 100%;
            height: 40px;
            &::after {
                display: none;
            }
        }
    }

    // .header__register

    &__register {
        font-size: 16px;
        font-weight: 500;
        width: 130px;
        height: 52px;
        @media (max-width: $md-4) {
            font-size: 20px;
            width: 100%;
            height: 40px;
        }
    }

    // .header__items

    &__items {
        position: relative;
        
        display: flex;
        align-items: center;
        margin: 0 46px 0 0;
        @media (max-width: $md-3) {
			margin: 0 20px 0 0;
		}
        @media (max-width: $md-4) {
            flex-direction: column;
        }
        
    }

    // .header__item

    &__item {
        list-style: none;
        margin: 0 15px;
        @media (max-width: $md-3) {
			margin: 0 13px 0 0;
		}
        @media (max-width: $md-4) {
            margin: 10px 0;
        }
        a {
            color: #fff;
            text-decoration: none;
            font-weight: 400;
            font-size: 16px;
            padding: 4px 0;
            &:hover, &:focus {
                border-bottom: 2px solid #fff;
                border-radius: 2px;
            }
            @media (max-width: $md-4) {
                font-size: 20px;
            }
        }
    }
}

