.footer {
    width: 100%;
    background-color: $darkBlueColor;
    padding: 80px 0;
    @media (max-width: $md-5) {
        padding: 40px 0;
    }
    // .footer__container

    &__container {
        height: 100%;
    }

    // .footer__body

    &__body {
        width: 100%;
        height: 100%;
        color: #fff;
        font-family: "Rubik", sans-serif;
    }

    // .footer__main

    &__main {
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
        margin: 0 0 110px 0;
        @media (max-width: $md-3) {
           flex-direction: column;
           margin: 0 0 50px 0;
        }
    }

    &__menu {
        display: flex;
        align-items: center;
        @media (max-width: $md-3) {
            margin: 0 0 40px 0;
        }
        @media (max-width: $md-5) {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .footer__logo

    &__logo {
        @media (max-width: $md-3) {
            margin: 0 0 40px 0;
        }
        @media (max-width: $md-5) {
            margin: 0 0 20px 0;
        }
    }

    // .footer__column

    &__column {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        margin: 0 32px 0 0;
        @media (max-width: $md-5) {
            margin: 0 0 20px 0;
        }
    }

    // .footer__caption

    &__caption {
        font-weight: 500;
        font-size: 20px;
        margin: 0 0 24px 0;
    }

    // .footer__items

    &__items {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        padding: 0;
    }

    // .footer__item

    &__item {
        list-style: none;
        margin: 0 0 10px 0;
        a {
            color: #F2F2F2;
            text-decoration: none;
            font-size: 16px;
            
        }
    }

    // .footer__pay

    &__pay {

    }

    // .footer__text

    &__text {
        font-weight: 500;
        font-size: 32px;
        max-width: 355px;
        margin: 0 0 40px 0;
        @media (max-width: $md-5) {
            font-size: 25px;
        }
    }

    // .footer__row

    &__row {
        display: flex;
        align-items: center;
    }

    // .footer__card

    &__card {
        margin: 0 24px 0 0;
        width: 96px;
        height: 64px;
        background-color: rgba(22, 22, 70, 0.5);
        padding: 12px 28px;
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: $md-5) {
            margin: 0 10px 0 0;
            width: 90px;
        }
    }

    &__ico {
        @include bg("contain");
        width: 40px;
        height: 40px;
    }

    // .footer__others

    &__others {
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-wrap: wrap-reverse;
        @media (max-width: $md-5) {
            flex-wrap: nowrap;
            flex-direction: column-reverse;
            align-items: flex-start;
        }
    }

    // .footer__copyright

    &__copyright {
        font-size: 14px;
        letter-spacing: 1px;
        @media (max-width: $md-4) {
            margin: 10px 0 0 0;
        }
    }

    // .footer__contacts

    &__contacts {
        display: flex;
        align-items: center;
        justify-content: center;
        @media (max-width: $md-5) {
            margin: 0 0 15px 0;
        }
    }

    // .footer__contact

    &__contact {
        @include bg("contain");
        width: 24px;
        height: 24px;
        
        cursor: pointer;
        transition: .3s all ease 0s;
        margin: 0 0 0 15px;
        @media (max-width: $md-5) {
            margin: 0 15px 0 0;
        }
        &:hover {
            transform: translateY(2px);
        }
    }
}
